.content{
    min-height: 100vh;

}

.startpage {
    border-radius: 15px 15px 0px 0px;
    background: url('./../../assets/images/snow-flake-start-page.svg') bottom center no-repeat, #F0F9FF;
    padding-bottom: 82px; ////

    .wrapper {
        display: grid;
        grid-template-columns: 50% 50%; ////
        // border

        width: 96%;
        margin: 0 auto;
    }

    &__description {
        padding-top: 45px; ////
        padding-bottom: 50px;

        &__content {
            &__made-in {
                // display: flex;
                // justify-content: flex-end;
                position: relative;
                left: 450px;

                &__box {
                    display: flex;
                    align-items: center;

                    &__text {
                        background-color: #008CE6;
                        position: relative;
                        left: 13px;
                        transform: rotate(-90deg);
                        font-family: 'Cera Round Pro';
                        border-radius: 5px 5px 0px 0px;
                        font-weight: 500;
                        font-size: 11px;
                        color: #fff;
                        padding: 5px 5px;

                        span {}
                    }

                    &__image {
                        background: #FFFFFF;
                        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
                        border-radius: 0 5px 5px 0;

                        img {
                            padding: 14px 20px;
                        }
                    }
                }
            }

            &__title {
                max-width: 460px;

                h1 {
                    margin-top: 10px;
                    margin-bottom: 30px;
                    font-family: 'Cera Round Pro';
                    font-style: normal;
                    font-weight: 900;
                    font-size: 72px;
                    line-height: 72px;
                    color: #2F2F2F;
                }
            }

            &__text {
                font-family: 'Cera Round Pro';
                font-style: normal;
                font-weight: 500;
                font-size: 18px;
                line-height: 30px;
                /* or 167% */
                color: #5B5B5B;
                max-width: 390px;
            }

            &__values {
                display: flex;
                align-items: center;
                gap: 40px;
                margin-bottom: 30px;
                transition: all 1s;

                &__stars {
                    label {
                        input {
                            display: none;

                        }

                        svg {}

                        &:nth-child(2n) {
                            margin-right: 10px;

                        }
                    }
                }

                &__price {

                    span {
                        // margin: 0;
                        font-family: 'Cera Round Pro';
                        font-style: normal;
                        font-weight: bold;
                        font-size: 48px;
                        line-height: 48px;
                        /* identical to box height */
                        color: #008CE6;

                    }
                }
            }

            &__properties {
                display: flex;
                justify-content: space-between;
                margin-top: 50px;
                max-width: 450px;
                // gap: 30px;

                &__item {
					display: flex;
					
                    &__pic {
                        display: flex;
                        // justify-self: center;
                        // align-self: center;
                        align-items: center;
                        justify-content: center;
                        width: 104px;
                        height: 100px;
                        background: #FFFFFF;
                        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
                        border-radius: 15px;

                    }

                    span {
						margin-top: 20px;
						margin-left: 20px;
                        text-align: left;
                        display: flex;
                        justify-content: center;
						width: 110px;
                        font-family: 'Cera Round Pro';
                        font-weight: 500;
                        font-size: 15px;
                    }
                }
            }

            &__button {
                margin-top: 50px;

                div {
                    cursor: pointer;
                    width: 278px;
                    height: 60px;
                    background: #FF4E5A;
                    box-shadow: 0px 16px 24px rgba(239, 45, 59, 0.2), 0px 2px 6px rgba(239, 45, 59, 0.12), 0px 0px 1px rgba(0, 0, 0, 0.04);
                    border-radius: 15px;
                    display: flex;
                    // align-content: center;
                    align-items: center;
                    justify-content: center;
                    gap: 20px;

                    span {
                        font-family: 'Cera Round Pro';
                        font-weight: bold;
                        font-size: 18px;
                        color: white;
                    }

                    div {
                        background: linear-gradient(135deg, #F24A56 0%, #FF525F 100%);
                        /* 3 */

                        box-shadow: -5px 5px 10px rgba(224, 70, 82, 0.2), 5px -5px 10px rgba(224, 70, 82, 0.2), -5px -5px 10px rgba(255, 86, 100, 0.9), 5px 5px 13px rgba(224, 70, 82, 0.9), inset 1px 1px 2px rgba(255, 86, 100, 0.3), inset -1px -1px 2px rgba(224, 70, 82, 0.5);
                        border-radius: 15px;
                        width: 30px;
                        height: 30px;
                        display: flex;
                        align-content: center;
                        justify-content: center;

                        svg {}
                    }
                }
            }
        }
    }

    &__items {
        // flex-basis: 10%;
        display: flex;
        // flex-direction: row;
        justify-content: flex-end;
        // gap: 11%; ////

        svg {
            // transform: rotate(-45deg);
        }

        &__main {
            display: flex;
            align-items: center;

            &__section {
                &__item {
                    display: flex;
                    // align-content: center;
                    justify-content: left;
                    align-items: center;

                    &__rating {
                        display: flex;
                        background: #FFFFFF;
                        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
                        border-radius: 10px;
                        transform: rotate(90deg);
                        padding: 14px 26px;
                        font-family: 'Cera Round Pro';
                        font-weight: bold;
                        font-size: 24px;
                        line-height: 24px;
                        column-gap: 10px;
                        position: relative;
                        left: 60px;
                        z-index: 1;
                    }

                    &__content {
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .side_pic {
                            width: 152px; ////
                            height: 650px;
                            position: absolute;


                            // background: url('./../../assets/images/shadow.png') (bottom -90px) (left -90px) no-repeat cover;
                            // float: left;
                            // box-shadow: 10px 10px 22px 10px  rgba(0, 0, 0, 0.35);
                            // box-shadow: ;
                        }

                        .shadow {
                            margin-left: 10px;
                            margin-top: 30px;
                            width: 200px;
                        }
                    }


                }
            }
        }

        &__side {
            display: flex;
            align-items: center;

            &__section {
                height: 100%;
                // width: 150px;    ////
                display: flex;
                // width: min-content;
                flex-direction: column;
                justify-content: space-around;

                &__item {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &__rating {
                        display: flex;
                        background: #FFFFFF;
                        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
                        border-radius: 10px;
                        // transform: rotate(90deg);
                        padding: 10px 15px;
                        font-family: 'Cera Round Pro';
                        font-weight: bold;
                        font-size: 16px;
                        // line-height: 24px;
                        // column-gap: 5px;
                        position: relative;
                        left: 50px;
                        z-index: 1;
                    }

                    &__content {
                        display: flex;
                        align-items: center;
                        justify-content: center;


                        .side_pic {
                            width: 38px;
                            height: 164px;
                            position: absolute;
                        }
                        .shadow{
                            margin-left: 5px;
                            margin-top: 10px;
                        }
                    }

                }
            }
        }
    }

    .arrow-wrapper {
        display: flex;
        justify-content: center;
		cursor: pointer;
        // margin-bottom: 82px;
        .arrow_down {}
    }

}